@charset "UTF-8";
/* As variaveis de cor do projeto, não preciso dizer como isso é importante né!? Alterou a cor, altera em um lugar só */
* {
  border-radius: 0 !important; }

html, body {
  height: 100%; }

body {
  font-family: 'Lato', sans-serif;
  background: linear-gradient(to bottom, #ECF0F1, #F2F1EF);
  min-height: 100%;
  font-size: 15px; }

.background {
  background: url(.././assets/bg.jpg) no-repeat center center fixed;
  background-size: cover;
  opacity: 0.1;
  filter: alpha(opacity=30);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.app {
  position: absolute;
  width: 100%; }

.page-wrapper {
  margin: 50px 0 30px 0; }

.under-shadow, input.underline-field {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12); }

.alert-success {
  color: #2e7d32;
  background-color: #a5d6a7;
  border-color: #a5d6a7; }

input.underline-field {
  padding: 10px;
  border: none; }



