@charset "UTF-8";
/* As variaveis de cor do projeto, não preciso dizer como isso é importante né!? Alterou a cor, altera em um lugar só */
.card {
  overflow: hidden;
  margin-bottom: 20px; }
  .card .card-top {
    background-color: rgba(255, 255, 255, 0.8);
    padding: 20px 10px;
    display: table;
    width: 100%; }
    .card .card-top .card-image {
      width: 128px;
      display: table-cell;
      box-sizing: border-box;
      vertical-align: top;
      height: 190px; }
      .card .card-top .card-image img {
        max-height: 190px; }
    .card .card-top .card-info {
      padding-left: 20px;
      display: table-cell;
      vertical-align: top;
      box-sizing: border-box; }
      .card .card-top .card-info .card-title {
        font-weight: 300;
        text-transform: uppercase; }
  .card .card-content {
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 0.7);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #ABB7B7; }
  .card .card-footer {
    border-top: 1px solid rgba(160, 160, 160, 0.2);
    background-color: rgba(255, 255, 255, 0.9);
    padding: 10px 20px; }
    .card .card-footer .glyphicon {
      font-size: 1.3em;
      cursor: pointer; }
      .card .card-footer .glyphicon.gly-details {
        color: #9fa8da;
        margin-right: 10px; }
        .card .card-footer .glyphicon.gly-details:hover {
          color: #283593; }
      .card .card-footer .glyphicon.gly-favorite {
        color: #ffcc80; }
        .card .card-footer .glyphicon.gly-favorite:hover, .card .card-footer .glyphicon.gly-favorite.favourited {
          color: #ef6c00; }
    .card .card-footer a {
      transition: color 0.3s ease;
      text-transform: uppercase;
      cursor: pointer; }
      .card .card-footer a:hover, .card .card-footer a:focus {
        text-decoration: none; }



